@media screen and (max-width: 1200px) {
  .navigation {
    display: flex;
  }
  .bottom-menu,
  .top-menu {
    padding-left: var(--padding-45xl);
    padding-right: var(--padding-45xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .hamburger-menu-v5,
  .studio-hillenburg {
    display: flex;
  }
  .desktop-menu,
  .navigation {
    display: none;
  }
  .body,
  .body1,
  .top-menu {
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
  .body,
  .body1 {
    padding-top: var(--padding-13xl);
  }
  .playsatoshisquest,
  .satoshis-quest {
    display: flex;
  }
  .bottom-menu {
    padding-left: var(--padding-13xl);
    padding-right: var(--padding-13xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .desktop-menu,
  .studio-hillenburg {
    display: none;
  }
  .hamburger-menu-v5:active {
    animation: 1s ease-in 0s 1 normal none pulsate;
    opacity: 1;
  }
  @keyframes pulsate {
    0% {
      transform: scale(1);
      transform-origin: center center;
      animation-timing-function: ease-out;
    }
    10% {
      transform: scale(0.91);
      animation-timing-function: ease-in;
    }
    17% {
      transform: scale(0.98);
      animation-timing-function: ease-out;
    }
    33% {
      transform: scale(0.87);
      animation-timing-function: ease-in;
    }
    45% {
      transform: scale(1);
      animation-timing-function: ease-out;
    }
  }
  .top-menu {
    padding-left: var(--padding-base);
    padding-right: var(--padding-base);
    box-sizing: border-box;
  }
  .navigation1 {
    display: flex;
  }
  .body,
  .body1 {
    padding-left: var(--padding-base);
    padding-top: var(--padding-13xl);
    padding-right: var(--padding-base);
    box-sizing: border-box;
  }
  .playsatoshisquest,
  .satoshis-quest {
    display: flex;
  }
  .bottom-menu-inner {
    display: none;
  }
  .navigation2 {
    display: flex;
  }
  .bottom-menu {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding-left: var(--padding-base);
